import { list } from "@/config/examples/classrooms";

export default {
  name: "classrooms",
  data: () => ({

    searchText: "",
  }),
  computed: {
    classrooms() {
      return list.filter(classroom => classroom.name.toLowerCase().includes(this.searchText.toLowerCase()));
    },
    classroomId() {
      return this.$route.params.id;
    }
  }
}
